
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
form,
fieldset,
input,
p,
blockquote,
th,
td {
    margin: 0;
    padding: 0;
}
body {
    font-size: 12px;
    font-family: Microsoft YaHei, SimSun;
}
* {
    -webkit-overflow-scrolling: touch;
}
fieldset,
img {
    border: 0;
}
ol,
ul {
    list-style: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: 100%;
}
em {
    font-style: normal;
}
input,
button,
select,
textarea {
    outline: none;
}
textarea {
    resize: none;
}

/* //禁用了文本的拖拉，尤其在谷歌下 */
p {
    text-justify: distribute;
}

/* //为了使文本段落左右两边对齐 */
p {
    word-wrap: break-word
}
a {
    text-decoration: none;
}
i {
    display: inline-block;
}
span {
    display: inline-block;
}
.color_f {
    color: #fff;
}
address,
cite,
code,
em,
th {
    font-weight: normal;
    font-style: normal;
}
a {
    color: inherit;
    text-decoration: none;
    display: inline-block;
}
a:hover,
a:active,
a:focus,
a:visited {
    text-decoration: none;
}
a,
a:hover,
a:active,
a:visited,
a:link,
a:focus {
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-user-select: none;
    -moz-user-focus: none;
    -moz-user-select: none;
}
.clamp {
    display: -webkit-box;
    /*! autoprefixer: off */
    -webkit-box-orient: vertical;
    /* autoprefixer: on */
    overflow: hidden;
    -webkit-line-clamp: 1;
}
.clamp2 {
    display: -webkit-box;
    /*! autoprefixer: off */
    -webkit-box-orient: vertical;
    /* autoprefixer: on */
    overflow: hidden;
    -webkit-line-clamp: 2;
}
.clamp3 {
    display: -webkit-box;
    /*! autoprefixer: off */
    -webkit-box-orient: vertical;
    /* autoprefixer: on */
    overflow: hidden;
    -webkit-line-clamp: 3;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
.fr {
    float: right;
    *display: inline;
}
.fl {
    float: left;
    *display: inline;
}

/*文本超出显示省略号*/
.ehn,
.otw,
.tow {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

/*清除浮动*/
.clearfix {
    clear: both;
    *zoom: 1;
}
.clearfix:after {
    display: block;
    clear: both;
    content: " ";
    visibility: hidden;
    height: 0;
}
.locati {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
label {
    display: block;
